.store-selector {
   background: palegoldenrod;
   max-width: 500px;
   margin: 50px auto;
   padding: 2rem;
   border: 2px solid #000;
   -webkit-box-shadow: 1px 1px 10px;
           box-shadow: 1px 1px 10px;
}

.store-selector.existing {
   background: rgb(167, 218, 238);
}

.store-selector input,
.store-selector button {
   width: 100%;
   background: white;
}
.store-selector input[type="text"],
.store-selector button[type="text"] {
   text-align: center;
   font-size: 3rem;
}

div.store-selector-buttons {
   display: -ms-flexbox;
   display: flex;
   margin-top: 5px;
   -ms-flex-pack: justify;
       justify-content: space-between;
}

div.store-selector-buttons button {
   width: 49%;
}

div.existing-stores {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: column;
       flex-direction: column;
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-align: center;
       align-items: center;
   font-size: 2.5rem;
   text-align: center;
}

div.existing-stores .select-store {
   -ms-flex: 2 1;
       flex: 2 1;
   text-align: center;
   margin-bottom: 5px;
   width: 100%;
}

div.existing-stores button {
   -ms-flex: 1 1;
       flex: 1 1;
   width: 49%;
}



